import Utils from './utils';
import Property from './property';
import AssetItem from './asset-item';

export default class Asset {
    constructor(obj = {}) {
        this.id = Utils.randomString();

        this.name = obj.name || '';
        this.controllerId = obj.controllerId;
        this.markerType = obj.markerType || Property.MARKER_TYPE;
        this.description = obj.description || '';

        this.markerPatternUrl = obj.markerPatternUrl || '';
        this.markerImageUrl = obj.markerImageUrl || '';

        this.object3D = obj.object3D;

        this.isUsed = !!obj.isUsed;
        this.isLoaded = !!obj.isLoaded;

        this.onShow = (typeof obj.onShow === 'function') ? obj.onShow : function () {};
        this.onHide = (typeof obj.onHide === 'function') ? obj.onHide : function () {};

        this.hideTimeoutId = null;
        this.hideInterval = null;

        this.trensformAverage = null;

        this.__itemArr = obj.itemArr || [];

        this.__init();
    }

    get isVisible() {
        const me = this;

        return (me.object3D) ? !!me.object3D.visible : false;
    }

    get itemArr() {
        const me = this;

        return me.__itemArr;
    }

    set itemArr(itemArr = []) {
        const me = this;

        for (let i = 0; i < itemArr.length; i++) {
            itemArr[i].parentAsset = me;
        }

        me.__itemArr = itemArr;
    }

    show(callEvent = true) {
        const me = this;
        // 화면에 보이지 않다가 보이는 경우만 이벤트 발생.
        const isFirstShow = (!me.isVisible);

        // 아이템의 isVisible값에 영향을 준다.
        me.object3D.visible = true;

        if (callEvent && isFirstShow) {
            me.onShow(me);

            for (let i = 0; i < me.__itemArr.length; i++) {
                if (isFirstShow && me.__itemArr[i].isVisible) {
                    me.__itemArr[i].onShow();
                }
            }
        }
    }

    hide(callEvent = true) {
        const me = this;

        // 아이템의 isVisible값에 영향을 준다.
        me.object3D.visible = false;

        me.object3D.traverse(function(object3D) {
            // 애니메이션 시간을 초기화 한다.
            if (object3D.mixer) {
                object3D.mixer.time = 0;

                for (let i = 0; i < object3D.animations.length; i++) {
                    const action = object3D.mixer.clipAction(object3D.animations[i]);
                    action.reset();
                }
            }
        });

        if (callEvent) {
            me.onHide(me);

            for (let i = 0; i < me.__itemArr.length; i++) {
                if (!me.__itemArr[i].isVisible) {
                    me.__itemArr[i].onHide();
                }
            }
        }
    }

    toJSON() {
        const me = this;

        const obj = {};

        for (let key in me) {
            if (me.hasOwnProperty(key) && (key != 'id') && (key != 'object3D')){
                if (key == '__itemArr') {
                    key = 'itemArr';
                }

                obj[key] = me[key];
            }
        }

        return obj;
    }

    clone() {
        const me = this;

        return new Asset(me);
    }

    __init() {
        const me = this;
        const itemArr = [];

        let item;

        for (let i = 0; i < me.__itemArr.length; i++) {
            item = new AssetItem(me.__itemArr[i]);

            item.parentAsset = me;

            itemArr.push(item);
        }

        me.__itemArr = itemArr;
    }
}