import Property from './property';
import Utils from './utils';

import html2canvas from 'html2canvas';

export default class AssetLoader {
    constructor (baseUrl) {
        this.baseUrl = baseUrl || '.';
        this.__objCache = {};
    }

    load(asset, onProgress = function () {}) {
        const me = this;

        asset = me.__initObject3D(asset);

        const promiseArr = [];
        const itemArr = asset.itemArr;
        const itemTotal = itemArr.length;

        let itemCount = 0;

        for (let i = 0; i < itemArr.length; i++) {
            promiseArr.push(me.__loadItem(itemArr[i]).then(function (assetItem) {
                itemCount++;
                onProgress(itemCount, itemTotal, assetItem, asset);
            }));
        }

        return Promise.all(promiseArr).then(function () {
            return me.__onLoadPromiseAll(asset);
        });
    }

    clone(asset) {
        const me = this;

        const clone = me.__initObject3D(asset.clone());
        const reloadType = Property.CLONE_RELOAD_TYPE;
        const assetItemArr = asset.itemArr;
        const cloneItemArr = clone.itemArr;
        const promiseArr = [];

        // 3D객체 복제
        // 3D객체를 복제하는 데 타입을 구분하는 이유는 threejs로 불러온 파일 형식에 따라서 복제가 안되는 포맷이 있기 때문이다.
        // 복제가 안되는 대상은 재로드 하는 방식을 사용한다.(현재는 fbx파일)
        // 반대로 obj로더의 경우에는 로드를 연속으로 하면 오류가 발생한다.
        let assetItem;
        let cloneItem;

        clone.isLoaded = false;
        clone.isUsed = false;

        for (let i = 0; i < cloneItemArr.length; i++) {
            assetItem = assetItemArr[i];
            cloneItem = cloneItemArr[i];

            if (reloadType.indexOf(cloneItem.type) > -1) {
                promiseArr.push(me.__loadItem(cloneItem));

            } else {
                cloneItem.object3D.add(assetItem.object3D.children[0].clone());
                cloneItem.object3D.scale.copy(assetItem.object3D.scale);
                cloneItem.object3D.position.copy(assetItem.object3D.position);
                cloneItem.object3D.rotation.copy(assetItem.object3D.rotation);

                promiseArr.push(Promise.resolve());
            }
        }

        return Promise.all(promiseArr).then(function () {
            return me.__onLoadPromiseAll(clone);
        });
    }

    __initObject3D(asset) {
        const itemArr = asset.itemArr;
        const assetGroup = new THREE.Group();

        let assetItem;

        assetGroup.name = Property.ITEM_OBJECT_FIELD_NAME;

        for (let i = 0; i < itemArr.length; i++) {
            assetItem = itemArr[i];
            assetItem.object3D = new THREE.Group();

            assetGroup.add(assetItem.object3D);
        }

        asset.object3D = assetGroup;

        return asset;
    }

    __onLoadPromiseAll(asset) {
        const lightArr = [];

        asset.object3D.traverse(function (object3D) {
            if (object3D.isLight) {
                lightArr.push(object3D);
            }

            // 모든 객체 양면 표시.
            if (object3D.material) {
                object3D.material.side = THREE.DoubleSide;
            }
        });

        for(let i = 0; i < lightArr.length; i++) {
            lightArr[i].parent.remove(lightArr[i]);
        }

        asset.isLoaded = true;

        return Promise.resolve(asset);
    }

    __loadItem(assetItem) {
        const me = this;

        const onLoad = function (object3D) {
            const itemGroup = assetItem.object3D;
            const scale = assetItem.scale;
            const position = assetItem.position;
            const rotation = assetItem.rotation;

            itemGroup.traverse(function (object3D) {
                if (object3D.material) {
                    object3D.userData[Property.ORG_TRANSPARENT_NAME] = object3D.material.transparent;
                    object3D.userData[Property.ORG_OPACITY_NAME] = object3D.material.opacity;
                }
            });

            itemGroup.scale.set(scale.x, scale.y, scale.z);
            itemGroup.position.set(position.x, position.y, position.z);
            itemGroup.rotation.set(rotation.x, rotation.y, rotation.z, itemGroup.rotation.w);

            itemGroup.add(object3D);

            return Promise.resolve(assetItem);
        };

        const onError = function () {
            assetItem.object3D.add(new THREE.Object3D());

            return Promise.resolve(assetItem);
        };

        switch (assetItem.type) {
            case Property.ITEM_TYPE_3D_OBJ:
                return me.__loadObjMtl(assetItem).then(onLoad).catch(onError);

            case Property.ITEM_TYPE_3D_STL:
                return me.__loadSTL(assetItem).then(onLoad).catch(onError);

            case Property.ITEM_TYPE_3D_FBX:
                return me.__loadFBX(assetItem).then(onLoad).catch(onError);

            case Property.ITEM_TYPE_IMAGE:
                return me.__loadImage(assetItem).then(onLoad).catch(onError);

            case Property.ITEM_TYPE_SPRITE:
                return me.__loadSprite(assetItem).then(onLoad).catch(onError);

            case Property.ITEM_TYPE_HTML:
                return me.__loadHTML(assetItem).then(onLoad).catch(onError);

            case Property.ITEM_TYPE_YOUTUBE:
                return me.__loadYoutube(assetItem).then(onLoad).catch(onError);

            case Property.ITEM_TYPE_TEXT:
                return me.__loadText(assetItem).then(onLoad).catch(onError);

            default:
                console.error('type not found');
                return onError();
        }
    }

    __clearGroup(object3D) {
        for (let i = 0; i < object3D.children.length; i++) {
            object3D.remove(object3D.children[i]);
        }
    }

    __loadText(assetItem) {
        const fieldEl = document.getElementById(Property.ELEMENT_FIELD_ID) || document.body;
        const divEl = Utils.createDivElement();

        divEl.innerHTML = assetItem.content;
        // 배치 후 모습이 보지 않게 위치를 이동한다.
        divEl.style.position = 'absolute';
        divEl.style.left = '100%';
        divEl.style.top = '100%';
        fieldEl.appendChild(divEl);

        const width = divEl.children[0].offsetWidth;
        const height = divEl.children[0].offsetHeight;

        assetItem.width = assetItem.width || width;
        assetItem.height = assetItem.height || height;

        return html2canvas(divEl, {
            backgroundColor: null,
            logging: false,
            width: assetItem.width,
            height: assetItem.height

        }).then(function (canvas) {
            const g = new THREE.PlaneGeometry(assetItem.width, assetItem.height);
            const m = new THREE.MeshBasicMaterial({
                transparent: true,
                map: new THREE.CanvasTexture(canvas),
                side: THREE.DoubleSide
            });
            const object3D = new THREE.Mesh(g, m);

            assetItem.isAnimation = false;

            assetItem.onAnimation = function () {};

            fieldEl.removeChild(divEl);

            return Promise.resolve(object3D);
        });
    }

    __loadImage(assetItem) {
        const me = this;

        return new Promise(function (resolve, reject) {
            const loader = new THREE.TextureLoader();
            const imageUrl = me.baseUrl + '/' + assetItem.itemUrl;

            loader.load(imageUrl, function (texture) {
                const g = new THREE.PlaneGeometry(assetItem.width, assetItem.height);
                const m = new THREE.MeshBasicMaterial({
                    transparent: true,
                    map: texture,
                    side: THREE.DoubleSide
                });
                const object3D = new THREE.Mesh(g, m);

                assetItem.isAnimation = false;

                assetItem.object3D.add(object3D);

                resolve(object3D);
            }, null, reject);
        });
    }

    __loadSprite(assetItem) {
        const me = this;

        return new Promise(function (resolve, reject) {
            const loader = new THREE.TextureLoader();
            const imageUrl = me.baseUrl + '/' + assetItem.itemUrl;

            loader.load(imageUrl, function (texture) {
                const sm = new THREE.SpriteMaterial({
                    transparent: true,
                    map: texture
                });
                const object3D = new THREE.Sprite(sm);

                assetItem.isAnimation = false;

                assetItem.object3D.add(object3D);

                resolve(object3D);
            }, null, reject);
        });
    }

    __loadHTML(assetItem) {
        // 3D객체의 크기는 CSS랜더러에 내용이 배치된 이후에 크기를 구해서 변경될 수 있다.
        // 웹 페이지에서 1px은 3D공간의 1과 매치된다. 높이의 경우 1px 설정시 알수 없는 이유로 그보다 크게 화면에 표시된다.
        // 최소한 웹 페이지 100px에 scale을 0.01을 주어 표현하는 게 정확하다.
        const g = new THREE.PlaneGeometry(assetItem.width || 1, assetItem.height || 1);
        const m = new THREE.MeshBasicMaterial({
            transparent: true,
            opacity: 0,
            side: THREE.DoubleSide,
            color: 0x000000,
            blending: THREE.NoBlending
        });
        const object3D = new THREE.Mesh(g, m);

        object3D.name = Property.TRANSPARENT_OBJECT_NAME;

        assetItem.isAnimation = false;

        assetItem.onAnimation = function () {};

        return Promise.resolve(object3D);
    }

    __loadYoutube(assetItem) {
        assetItem.width = assetItem.width || Property.YOUTUBE_DEFAULT_W;
        assetItem.height = assetItem.height || Property.YOUTUBE_DEFAULT_H;

        const g = new THREE.PlaneGeometry(assetItem.width, assetItem.height);
        const m = new THREE.MeshBasicMaterial({
            transparent: true,
            opacity: 0,
            side: THREE.DoubleSide,
            color: 0x000000,
            blending: THREE.NoBlending
        });
        const object3D = new THREE.Mesh(g, m);

        object3D.name = Property.TRANSPARENT_OBJECT_NAME;

        assetItem.isAnimation = false;

        assetItem.onAnimation = function () {};

        return Promise.resolve(object3D);
    }

    __loadFBX(assetItem) {
        const me = this;

        return new Promise(function (resolve, reject) {
            const loader = new THREE.FBXLoader();
            const fbxUrl = me.baseUrl + '/' + assetItem.itemUrl;

            loader.load(fbxUrl, function (object3D) {

                object3D.mixer = new THREE.AnimationMixer(object3D);

                if (object3D.animations && object3D.animations.length) {
                    for (let i = 0; i < object3D.animations.length; i++) {
                        const action = object3D.mixer.clipAction(object3D.animations[i]);
                        action.play();
                    }

                    assetItem.onAnimation = function (clockDelta) {
                        const thisItem = this;

                        let time = clockDelta;

                        if (thisItem.isAnimation) {
                            const mixer = thisItem.object3D.children[0].mixer;

                            // 애니메이션이 시작되고 정해신 시간 만큼만 재생된다.
                            // mixer.time은 AssetManager 에서 에셋이 화면에 가려질 때 초기화 된다.
                            if (thisItem.animationTime > 0 && thisItem.animationTime < (mixer.time * 1000)) {
                                time = 0;
                            }

                            mixer.update(time);
                        }
                    };

                    assetItem.isAnimation = true;

                } else {
                    assetItem.isAnimation = false;

                    assetItem.onAnimation = function () {};
                }

                object3D.traverse(function (obj) {
                    if (obj.isMesh) {
                        obj.frustumCulled = false;
                    }
                });

                resolve(object3D);
            }, null, reject);
        });
    }

    __loadSTL(assetItem) {
        const me = this;

        return new Promise(function (resolve, reject) {
            const loader = new THREE.STLLoader();
            const stlUrl = me.baseUrl + '/' + assetItem.itemUrl;

            loader.load(stlUrl, function (geometry) {
                const object3D = new THREE.Mesh(geometry, new THREE.MeshLambertMaterial());

                assetItem.isAnimation = false;

                assetItem.onAnimation = function () {};

                resolve(object3D);
            }, null, reject);
        });
    }

    __loadObj (assetItem, materials, loader) {
        const me = this;

        return new Promise(function (resolve, reject) {
            const objUrl = me.baseUrl + '/' + assetItem.itemUrl;

            loader.logging.enabled = false;

            if (materials) {
                loader.setMaterials(materials);
            }

            loader.load(objUrl, function (evt) {
                const object3D = evt.detail.loaderRootNode;

                assetItem.isAnimation = false;

                assetItem.onAnimation = function () {};

                me.__objCache[assetItem.itemUrl] = object3D.clone();

                resolve(object3D);
            }, null, reject, null, false);
        });
    }

    __loadMtl(assetItem, loader) {
        const me = this;

        loader.logging.enabled = false;

        return new Promise(function (resolve, reject) {
            try {
                const xhr = new XMLHttpRequest();

                let mtlUrl = me.baseUrl + '/' + assetItem.itemUrl;

                mtlUrl = mtlUrl.substring(0, mtlUrl.lastIndexOf('.'));
                mtlUrl += Property.EXT_MTL;

                // mtl파일이 없는 오류를 로더에서 감지할 수 없기 때문에 따로 확인한다.
                xhr.onreadystatechange = function () {
                    if (xhr.readyState === xhr.DONE) {
                        if (xhr.status === 200) {
                            loader.loadMtl(mtlUrl, null, function (materials) {
                                resolve(materials);
                            });

                        } else {
                            reject(new Error('mtl not found'));
                        }
                    }
                };

                xhr.open('GET', mtlUrl);
                xhr.send();

            } catch (error) {
                reject(error);
            }
        });
    }

    __loadObjMtl(assetItem) {
        const me = this;

        const loader = new THREE.OBJLoader2();

        loader.logging.enabled = false;

        return new Promise(function (resolve, reject) {
            const cacheObject3D = me.__objCache[assetItem.itemUrl];

            if (cacheObject3D) {
                resolve(cacheObject3D.clone());

            } else {
                me.__loadMtl(assetItem, loader).catch(function () {
                    return Promise.resolve(null);

                }).then(function (materials) {
                    try {
                        me.__loadObj(assetItem, materials, loader).then(function (object3D) {
                            resolve(object3D);
                        });

                    } catch (error) {
                        console.error(error);
                        reject(error);
                    }
                });
            }
        });
    }
}