import 'script-loader!../../lib/zlibjs/inflate.min.js'; // 로더를 위해 필요한 외부 라이브러리.
import 'script-loader!../../lib/threejs/three.min.js';
import 'script-loader!../../lib/threejs/CSS3DRenderer.min.js';
import 'script-loader!../../lib/threejs/LoaderSupport.min.js';
import 'script-loader!../../lib/threejs/MTLLoader.min.js';
import 'script-loader!../../lib/threejs/OBJLoader2.min.js';
import 'script-loader!../../lib/threejs/STLLoader.min.js';
import 'script-loader!../../lib/threejs/FBXLoader.min.js';
import 'script-loader!../../lib/threejs/OrbitControls.min.js';
import 'script-loader!../../lib/threejs/TransformControls.min.js';

import Property from '../nemo-ar/property';
import Utils from '../nemo-ar/utils';
import Asset from '../nemo-ar/asset';
import AssetItem from '../nemo-ar/asset-item';
import AssetLoader from '../nemo-ar/asset-loader';
import AssetCssRenderer from '../nemo-ar/asset-css-renderer';
import Raycaster from '../nemo-ar/raycaster';

import Options from './options';
import AssetItemManager from './asset-item-manager';
import HistoryManager from './history-manager';
import CssUnitE from './css-unit-e';
import TransformHistory from './transformHistory';

export default class NemoArEditor {
    constructor (obj) {
        this.options = new Options(obj);
        this.fieldEl = Utils.createDivElement();
        this.scene = new THREE.Scene();
        this.light = new THREE.DirectionalLight();
        this.camera = new THREE.PerspectiveCamera(45, this.options.width / this.options.height, Property.CAMERA_NEAR, Property.CAMERA_FAR);
        this.renderer = new THREE.WebGLRenderer({antialias: true, alpha: true});
        this.objectField = new THREE.Group();
        this.obControls = new THREE.OrbitControls(this.camera, this.renderer.domElement);
        this.tfControls = new THREE.TransformControls(this.camera, this.renderer.domElement);
        this.raycaster = new Raycaster(this.renderer, this.camera);
        this.clock = new THREE.Clock();

        this.assetLoader = new AssetLoader(this.options.baseUrl);
        this.assetCssRenderer = new AssetCssRenderer(this.renderer, this.camera, CssUnitE);
        this.assetItemManager = new AssetItemManager();
        this.historyManager = new HistoryManager(this);

        this.openAsset = new Asset();

        this.gridObject3D = null;
        this.markerImageObject3D = null;
        this.selectedItem = null;

        this.disableAttach = this.options.disableAttach;

        this.__init();
    }

    /**
     * 실행.
     */
    redo() {
        const me = this;

        me.detach();
        me.historyManager.redo();
    }

    /**
     * 실행취소.
     */
    undo() {
        const me = this;

        me.detach();
        me.historyManager.undo();
    }

    /**
     * 에디터에 표시된 내용을 JSON형태로 출력한다.
     */
    exportJson() {
        const me = this;

        const asset = new Asset(me.openAsset);

        asset.itemArr = me.assetItemManager.getItemArray();

        return JSON.stringify(asset);
    }

    /**
     * 에셋을 불러온다.
     * @param {Asset || object} obj 불러올 에셋 정보.
     */
    open(obj) {
        const me = this;

        let asset = new Asset(obj);

        me.drawMarker(asset.markerImageUrl, asset.markerPatternUrl);

        // historyManager가 잠겨있지 않는 경우만 처리한다.
        if (!me.historyManager.isLock) {
            const markerHistoryInfo = me.historyManager.getHistory();
            
            // 1. 히스토리 작업.
            // 불러온 파일이 마커와 아이템이 있는 경우, 마커를 그리고 바로 아이템을 그리도록 처리한다.
            // [1. 히스토리 작업] -> [3. 히스토리 작업]

            // or 불러온 파일에 마커만 있지만 이전 아이템이 그려져 있는 경우, 마커를 그리고 바로 이전 아이템을 지우도록 처리한다.
            // [1. 히스토리 작업] -> [2. 히스토리 작업]
            if (asset.itemArr.length || me.objectField.children.length) {
                markerHistoryInfo.onRedo = function () {
                    me.historyManager.redo();
                };
            }
        }

        return new Promise(function (resolve, reject) {
            // 마커 이미지가 그려질 여유를 준다.
            setTimeout(function () {
                // 그려져 있는 대상이 있으면 전부 지우고 새로 불러온 대상을 그린다.
                if (me.objectField.children.length) {
                    // 이전 대상을 지운다.
                    me.removeAll();

                    // historyManager가 잠겨있지 않는 경우만 처리한다.
                    if (!me.historyManager.isLock) {
                        // 지움 작업을 했던 히스토리를 가지고 온다.
                        const removeHistoryInfo = me.historyManager.getHistory();
    
                        // 2. 히스토리 작업
                        // 대상을 지웠던 작업 전, 후의 작업을 연속으로 호출하도록 세팅한다.
                        // [1. 히스토리 작업] <-> [2. 히스토리 작업] <-> [3. 히스토리 작업]
                        removeHistoryInfo.onRedo = function () {
                            me.historyManager.redo();
                        };
                        removeHistoryInfo.onUndo = function () {
                            me.historyManager.undo();
                        };
                    }
                }
    
                me.assetLoader.load(asset).then(function (currentAsset) {
                    const itemArr = currentAsset.itemArr;
    
                    me.openAsset = currentAsset;
    
                    me.assetCssRenderer.add(currentAsset);
    
                    // 아이템이 있는 에셋의 경우에는 후속 처리를 한다.
                    // 마커 이미지를 로드하는 히스토리 따로, 아이템을 로드하는 히스토리 따로 관리하기 때문에 실행 취소를 두 번 연다라 해야하는 경우가 있다.
                    if (itemArr.length && currentAsset.object3D.children.length) {
                        me.objectField.add.apply(me.objectField, currentAsset.object3D.children);
    
                        me.assetItemManager.addArray(itemArr);
    
                        // 3. 히스토리 작업
                        // [1. 히스토리 작업] <-> [3. 히스토리 작업] or [2. 히스토리 작업] <-> [3. 히스토리 작업]
                        me.historyManager.push({
                            name: 'onLoad',
                            redo: function () {
                                me.recoverArray(itemArr);
                            },
                            undo: function () {
                                me.removeArray(itemArr);
                            },
                            onUndo: function () {
                                me.undo();
                            }
                        });
                    }

                    resolve(currentAsset);

                }).catch(reject);
            }, 100);
        });
    }

    /**
     * 화면에 3D객체를 추가한다.
     * @param {AssetItem || object} obj 화면에 표시할 3D객체 정보.
     */
    import(obj) {
        const me = this;

        const asset = new Asset({itemArr: [obj]});

        return me.assetLoader.load(asset).then(function (currentAsset) {
            const itemArr = currentAsset.itemArr;

            me.objectField.add.apply(me.objectField, currentAsset.object3D.children);

            me.assetItemManager.addArray(itemArr);

            me.assetCssRenderer.add(asset);

            me.historyManager.push({
                name: 'import',
                redo: function () {
                    me.recoverArray(itemArr);
                },
                undo: function () {
                    me.removeArray(itemArr);
                }
            });

            return Promise.resolve(asset.itemArr[0]);
        });
    }

    /**
     * 화면의 모든 아이템을 제거한다.
     */
    removeAll() {
        const me = this;

        me.detach();

        const itemArr = me.assetItemManager.getItemArray();

        me.objectField.remove.apply(me.objectField, me.objectField.children);
        me.assetItemManager.removeAll();

        me.historyManager.push({
            name: 'removeAll',
            redo: function () {
                me.removeArray(itemArr);
            },
            undo: function () {
                me.recoverArray(itemArr);
            }
        });
    }

    /**
     * 선택한 아이템을 제거한다.
     */
    remove() {
        const me = this;

        const assetItem = me.selectedItem;

        if (assetItem) {
            const id = assetItem.id;

            me.detach();

            me.objectField.remove(assetItem.object3D);

            me.assetItemManager.remove(id);

            me.historyManager.push({
                name: 'remove',
                redo: function () {
                    me.removeById(id);
                },
                undo: function () {
                    me.recoverFromId(id);
                }
            });
        }
    }

    /**
     * 아이템 id를 받아서 화면에서 제거한다.
     * @param {string} id 삭제할 아이템 id.
     */
    removeById(id) {
        const me = this;
        const item = me.assetItemManager.remove(id);

        me.objectField.remove(item.object3D);

        me.historyManager.push({
            name: 'removeById',
            redo: function () {
                me.removeById(id);
            },
            undo: function () {
                me.recoverFromId(id);
            }
        });
    }

    /**
     * 이름을 입력받아 해당 이름을 가진 아이템 정보를 반환한다. 중복시 처음 찾은 정보만 반환한다.
     * @param {string} name assetItem에 지정된 이름.
     */
    getItemByName (name) {
        const me = this;

        return me.assetItemManager.getItemByName(name);
    }

    /**
     * 아이템 배열을 받아 화면에서 제거한다.
     * @param {Array} arr 삭제할 아이템 배열.
     */
    removeArray(arr) {
        const me = this;

        for (let i = 0; i < arr.length; i++) {
            me.removeById(arr[i].id);
        }
    }

    /**
     * 아이템 id를 받아서 화면에 복원한다.
     * @param {string} id 복원할 아이템 id.
     */
    recoverFromId(id) {
        const me = this;
        const item = me.assetItemManager.recover(id);

        me.objectField.add(item.object3D);

        me.historyManager.push({
            name: 'recoverFromId',
            redo: function () {
                me.recoverFromId(id);
            },
            undo: function () {
                me.removeById(id);
            }
        });
    }

    /**
     * 아이템 배열을 받아 화면에 되돌려 놓는다.
     * @param {Array} arr 복원할 아이템 배열.
     */
    recoverArray(arr) {
        const me = this;

        for (let i = 0; i < arr.length; i++) {
            me.recoverFromId(arr[i].id);
        }
    }

    /**
     * 조작 모드를 변경한다.
     * @param {string} mode 모드명.
     */
    modeChange(mode) {
        const me = this;

        if (mode !== Property.CONTROLS_TF_MODE_SCALE) {
            me.tfControls.space = Property.CONTROLS_TF_SPACE_W;
        }

        me.tfControls.setMode(mode);
    }

    /**
     * 선택한 아이템의 크기를 변경한다.
     * @param {number} x x축.
     * @param {number} y y축.
     * @param {number} z z축.
     */
    setScale(x, y, z) {
        const me = this;

        const assetItem = me.selectedItem;

        if (assetItem) {
            x = parseFloat(x);
            y = parseFloat(y);
            z = parseFloat(z);

            const transformHistory = new TransformHistory(assetItem, assetItem);

            assetItem.object3D.scale.set(x, y, z);

            assetItem.syncAssetInfoAndObject3D();

            transformHistory.setNextItem(assetItem);

            me.__pushTransformHistory([transformHistory]);

            me.options.onMove([assetItem], me);
        }
    }

    /**
     * 입력된 비율만큼 전체 크기를 변경한다. (1 == 100%)
     * @param {number} x x축.
     * @param {number} y y축.
     * @param {number} z z축.
     */
    multiplyScaleAll(x, y, z) {
        const me = this;

        const itemArr = me.assetItemManager.getItemArray();
        const tHistoryArr = [];

        let assetItem;

        x = parseFloat(x);
        y = parseFloat(y);
        z = parseFloat(z);

        me.detach();

        for (let i = 0; i < itemArr.length; i++) {
            assetItem = itemArr[i];

            const transformHistory = new TransformHistory(assetItem, assetItem);

            assetItem.object3D.position.multiply(new THREE.Vector3(x, y, z));
            assetItem.object3D.scale.multiply(new THREE.Vector3(x, y, z));

            assetItem.syncAssetInfoAndObject3D();

            transformHistory.setNextItem(assetItem);

            tHistoryArr.push(transformHistory);
        }

        me.__pushTransformHistory(tHistoryArr);

        me.options.onMove(itemArr, me);
    }

    /**
     * 선택한 아이템을 이동한다.
     * @param {number} x x좌표.
     * @param {number} y y좌표.
     * @param {number} z z좌표.
     */
    setPosition(x, y, z) {
        const me = this;

        const assetItem = me.selectedItem;

        if (assetItem) {
            x = parseFloat(x);
            y = parseFloat(y);
            z = parseFloat(z);

            const transformHistory = new TransformHistory(assetItem, assetItem);

            assetItem.object3D.position.set(x, y, z);

            assetItem.syncAssetInfoAndObject3D();

            transformHistory.setNextItem(assetItem);

            me.__pushTransformHistory([transformHistory]);

            me.options.onMove([assetItem], me);
        }
    }

    /**
     * 선택한 아이템을 축을 기준으로 회전시킨다.
     * @param {number} x x축 기준 회전값.
     * @param {number} y y축 기준 회전값.
     * @param {number} z z축 기준 회전값.
     */
    setRotation(x, y, z) {
        const me = this;

        const assetItem = me.selectedItem;

        if (assetItem) {
            x = parseFloat(x);
            y = parseFloat(y);
            z = parseFloat(z);

            const transformHistory = new TransformHistory(assetItem, assetItem);

            assetItem.object3D.rotation.set(x, y, z, assetItem.object3D.rotation.w);

            assetItem.syncAssetInfoAndObject3D();

            transformHistory.setNextItem(assetItem);

            me.__pushTransformHistory([transformHistory]);

            me.options.onMove([assetItem], me);
        }
    }

    /**
     * 선택한 아이템이 애니메이션인 경우 실행할 시간을 입력한다. 0인 경우 계속 실행한다.
     * @param {integer} ms 애니메이션을 실행하는 시간. (millisecond)
     */
    setAnimationTime(ms) {
        const me = this;

        const assetItem = me.selectedItem;

        if (assetItem && assetItem.isAnimation) {
            ms = parseInt(ms, 10);

            assetItem.animationTime = ms;

            if (assetItem.object3D.children.length) {
                const object3D = assetItem.object3D.children[0];

                object3D.mixer.time = 0;

                for (let i = 0; i < object3D.animations.length; i++) {
                    const action = object3D.mixer.clipAction(object3D.animations[i]);
                    action.reset();
                }
            }
        }
    }

    /**
     * 대상을 클릭했을 때 이동할 경로를 입력한다.
     * @param {string} link 이동할 경로.
     */
    setLink(link) {
        const me = this;

        const assetItem = me.selectedItem;

        if (assetItem) {
            assetItem.link = link;
        }
    }

    /**
     * 아이템을 선택한다.
     * @param {AssetItem} assetItem 선택할 아이템 객체.
     */
    attach(assetItem) {
        const me = this;

        if (assetItem && (!me.disableAttach)) {
            me.tfControls.attach(assetItem.object3D);
            me.tfControls.visible = true;
            me.selectedItem = assetItem;

            me.options.onSelect(assetItem, me);
        }
    }

    /**
     * 선택을 취소한다.
     */
    detach() {
        const me = this;

        if (!me.disableAttach) {
            me.tfControls.detach();
            me.tfControls.visible = false;
            me.selectedItem = null;

            me.options.onDeselect(me);
        }
    }

    /**
     * 카메라 위치를 초기화 한다.
     */
    viewDefault() {
        const me = this;

        me.obControls.target0.copy(Property.CAMERA_ZERO_LOOK_AT);
        me.obControls.position0.copy(Property.CAMERA_ZERO_POSITION);
        me.obControls.zoom0 = Property.CAMERA_ZERO_ZOOM;

        me.obControls.reset();
    }

    /**
     * 선택된 대상을 화면의 기준점으로 잡는다.
     * @param {AssetItem} assetItem 보여줄 아이템 객체.
     */
    viewTarget(assetItem = this.selectedItem) {
        const me = this;

        if (assetItem) {
            const object3D = assetItem.object3D;

            let box = new THREE.Box3();
            box.setFromObject(object3D);

            const boxSize = new THREE.Vector3();
            box.getSize(boxSize);

            const halfBoxSize = boxSize.clone().divide(new THREE.Vector3(2, 2, 2));
            const target = box.max.clone().sub(halfBoxSize);

            // 카메라의 기본 위치를 기준으로 한 화면에 표시되 치수. (3D공간 내부의 수치 기준)
            const vRad = (Math.PI / 180) * (me.camera.getEffectiveFOV()) / 2; // 화면의 수직각도.
            const defaultHeight = Property.CAMERA_ZERO_POSITION.z * Math.tan(vRad); // 화면의 높이.
            const defaultWidth = me.camera.aspect * defaultHeight; // 화면의 넓이.
            const defaultDepth = (defaultHeight / 2) / Math.tan(vRad); // 화면의 깊이.
            const hRad = Math.atan2(defaultWidth / 2, defaultDepth); // 화면의 수평각도.

            let z = boxSize.y / Math.tan(vRad); // 수직 기준 깊이. 주변 여백을 위해 y값을 2로 나누지 않고 사용한다.

            if (defaultWidth - boxSize.x < defaultHeight - boxSize.y) {
                z = boxSize.x / Math.tan(hRad); // 수평 기준 깊이. 주변 여백을 위해 x값을 2로 나누지 않고 사용한다.
            }

            z += halfBoxSize.z; // 깊이는 대상의 중심이 아니라 전면을 기준으로 한다.

            const position = new THREE.Vector3(0, 0, z);
            position.add(target);

            me.obControls.target0.copy(target);
            me.obControls.position0.copy(position);
            me.obControls.zoom0 = Property.CAMERA_ZERO_ZOOM;

            me.obControls.reset();
        }
    }

    /**
     * 선택을 못하게 한다.
     */
    disableAttach() {
        const me = this;

        me.disableAttach = true;
    }

    /**
     * 선택을 할 수 있도록 한다.
     */
    enableAttach() {
        const me = this;

        me.disableAttach = false;
    }

    /**
     * 화면컨트롤러를 끈다.
     */
    disableViewController() {
        const me =this;

        me.obControls.enableZoom = false;
        me.obControls.enableRotate = false;
        me.obControls.enabled = false;
        me.obControls.saveState();
    }

    /**
     * 화면컨트롤러를 켠다.
     */
    enableViewController() {
        const me = this;

        me.obControls.reset();
        me.obControls.enableZoom = true;
        me.obControls.enableRotate =true;
        me.obControls.enabled = true;
    }

    /**
     * 마커 이미지를 교체 한다.
     * @param {string} imgUrl 마커 이미지 URL.
     * @param {string} pattUrl 마커 패턴 URL.
     */
    drawMarker(imgUrl, pattUrl) {
        const me = this;

        const texture = new THREE.TextureLoader().load(me.options.baseUrl + '/' + imgUrl);

        let prevImgUrl = null;
        let prevPattUrl = null;

        if (me.markerImageObject3D) {
            prevImgUrl = me.openAsset.markerImageUrl;
            prevPattUrl = me.openAsset.markerPatternUrl;

            me.markerImageObject3D.material.map = texture;

        } else {
            const geometry = new THREE.PlaneGeometry(1,1);
            const material = new THREE.MeshBasicMaterial({
                map: texture,
                side: THREE.DoubleSide,
                color: 0xffffff
            });
            const mesh = new THREE.Mesh(geometry, material);

            mesh.rotation.set(-(90 * (Math.PI / 180)), 0, 0, mesh.rotation.w);
            me.scene.add(mesh);
            me.markerImageObject3D = mesh;
        }

        me.openAsset.markerImageUrl = imgUrl;
        me.openAsset.markerPatternUrl = pattUrl;

        me.historyManager.push({
            name: 'drawMarker',
            redo: function () {
                me.drawMarker(imgUrl, pattUrl);
            },
            undo: function () {
                if (prevImgUrl && prevPattUrl) {
                    me.drawMarker(prevImgUrl, prevPattUrl);

                } else {
                    me.scene.remove(me.markerImageObject3D);
                    me.markerImageObject3D = null;
                    me.openAsset.markerImageUrl = '';
                    me.openAsset.markerPatternUrl = '';
                }
            }
        });
    }

    /**
     * 화면에 그리드를 그린다.
     * @param {number} size 그리드 영역 사이즈.
     * @param {number} step 그리드 간격.
     */
    drawGrid(size = Property.GRID_SIZE, step = Property.GRID_STEP) {
        const me = this;
        const geometry = new THREE.Geometry();
        const material = new THREE.LineBasicMaterial({color: Property.GRID_COLOR});

        for (let i = (-size); i <= size; i += step) {
            geometry.vertices.push(new THREE.Vector3(-size, 0, i));
            geometry.vertices.push(new THREE.Vector3(size, 0, i));

            geometry.vertices.push(new THREE.Vector3(i, 0, -size));
            geometry.vertices.push(new THREE.Vector3(i, 0, size));
        }

        material.transparent = true;
        material.opacity = Property.GRID_OPACITY;

        if (me.gridObject3D) {
            me.scene.remove(me.gridObject3D);
        }

        me.gridObject3D = new THREE.LineSegments(geometry, material);

        me.scene.add(me.gridObject3D);
    }

    /**
     * 화면의 크기를 재설정 한다.
     * @param {integer} width 화면의 넓이.
     * @param {integer} height 화면의 높이.
     */
    resize(width, height) {
        const me = this;

        me.options.width = width;
        me.options.height = height;

        me.renderer.setSize(me.options.width, me.options.height);

        me.assetCssRenderer.resize();
        me.assetCssRenderer.domElement.style.left = '0px';
        me.assetCssRenderer.domElement.style.top = '0px';

        me.fieldEl.style.width = me.options.width + 'px';
        me.fieldEl.style.height = me.options.height + 'px';

        me.camera.aspect = me.options.width / me.options.height;
        me.camera.updateProjectionMatrix();
    }

    /**
     * 화면에 격자를 보인다.
     */
    showGrid() {
        const me = this;

        me.gridObject3D.visible = true;
    }

    /**
     * 화면에 격자를 숨긴다.
     */
    hideGrid() {
        const me = this;

        me.gridObject3D.visible = false;
    }

    /**
     * 배경화면을 정해진 색으로 표시한다.
     * @param {string} colorCode #000000 형태의 색상코드.
     */
    showBackground(colorCode = Property.STYLE_BACKGROUND_COLOR) {
        const me = this;

        me.fieldEl.style.backgroundColor = colorCode;
    }

    /**
     * 배경화면을 숨긴다.
     */
    hideBackground() {
        const me = this;

        me.fieldEl.style.backgroundColor = '';
    }

    __intersect(evt) {
        const me = this;

        const intersectChild = me.raycaster.intersect(this.objectField.children, evt.offsetX, evt.offsetY);

        let group = null;

        if (intersectChild) {
            intersectChild.traverseAncestors(function (object3D) {
                if (object3D.parent && object3D.parent.name === Property.ITEM_OBJECT_FIELD_NAME) {
                    group = object3D;
                }
            });
        }

        return me.assetItemManager.getItemByObject3D(group);
    }

    __pushTransformHistory(arr) {
        const me = this;

        const tArr = arr;

        me.historyManager.push({
            name: 'transform',
            redo: function () {
                let tHistory;
                let object3D;
                for (let i = 0; i < arr.length; i++) {
                    tHistory = tArr[i];
                    object3D = tHistory.currentItem.object3D;

                    const p = tHistory.nextItem.position;
                    const r = tHistory.nextItem.rotation;
                    const s = tHistory.nextItem.scale;

                    object3D.position.set(p.x, p.y, p.z);
                    object3D.rotation.set(r.x, r.y, r.z, object3D.rotation.w);
                    object3D.scale.set(s.x, s.y, s.z);

                    tHistory.currentItem.syncAssetInfoAndObject3D();
                }
            },
            undo: function () {
                let tHistory;
                let object3D;

                for (let i = 0; i < arr.length; i++) {
                    tHistory = tArr[i];
                    object3D = tHistory.currentItem.object3D;

                    const p = tHistory.prevItem.position;
                    const r = tHistory.prevItem.rotation;
                    const s = tHistory.prevItem.scale;

                    object3D.position.set(p.x, p.y, p.z);
                    object3D.rotation.set(r.x, r.y, r.z, object3D.rotation.w);
                    object3D.scale.set(s.x, s.y, s.z);

                    tHistory.currentItem.syncAssetInfoAndObject3D();
                }
            }
        });
    }

    __init() {
        const me = this;

        me.renderer.setSize(me.options.width, me.options.height);
        me.renderer.setPixelRatio(window.devicePixelRatio);
        me.renderer.domElement.style.position = 'absolute';
        me.renderer.domElement.style.left = '0px';
        me.renderer.domElement.style.top = '0px';

        me.assetCssRenderer.resize();
        me.assetCssRenderer.domElement.style.left = '0px';
        me.assetCssRenderer.domElement.style.top = '0px';

        me.obControls.enableKeys = false;
        me.obControls.enablePan = true;
        me.obControls.minPolarAngle = Property.CONTROLS_MIN_POLAR_ANGLE;
        me.obControls.maxPolarAngle = Property.CONTROLS_MAX_POLAR_ANGLE;

        if (me.options.disableViewController) {
            me.disableViewController();
        }

        me.camera.position.copy(me.options.cameraPosition);
        me.camera.lookAt(me.options.cameraLookAt);

        me.objectField.name = Property.ITEM_OBJECT_FIELD_NAME;

        // material 에 transparent 속성이 true인 경우 선택한 객체 뒤로 컨트롤러가 가려진다. 이를 해결하기 위해 renderOrder값을 1로 올려준다. (기본값은 0)
        me.tfControls.traverse(function (object3D) {
            object3D.renderOrder = 1;
        });

        me.scene.add(me.camera);
        me.scene.add(me.light);
        me.scene.add(me.objectField);
        me.scene.add(me.tfControls);

        me.fieldEl.classList.add(Property.ELEMENT_FIELD_CLASS_NAME);
        me.fieldEl.id = Property.ELEMENT_FIELD_ID;
        me.fieldEl.style.width = me.options.width + 'px';
        me.fieldEl.style.height = me.options.height + 'px';
        me.fieldEl.style.overflow = 'hidden';
        me.fieldEl.style.position = 'relative';

        if (!me.options.hideBackground) {
            me.showBackground();
        }

        me.fieldEl.appendChild(me.assetCssRenderer.domElement);
        me.fieldEl.appendChild(me.renderer.domElement);

        me.options.rootElement.appendChild(me.fieldEl);

        if (!me.options.hideGrid) {
            me.drawGrid();
        }

        (function animate() {
            me.renderer.render(me.scene, me.camera);
            me.assetItemManager.animation(me.clock.getDelta());
            me.light.position.copy(me.camera.position);

            me.assetCssRenderer.updateAll();
            me.assetCssRenderer.render();

            requestAnimationFrame(animate);
        })();

        // ====================================================================================================
        me.renderer.domElement.addEventListener('mousedown', function (evt) {
            const intersectedItem = me.__intersect(evt);

            switch(evt.which) {
                case 1:
                    break;

                case 2:
                    break;

                case 3:
                    if (intersectedItem) {
                        me.attach(intersectedItem);

                    } else {
                        me.detach();
                    }
                    break;
            }
        });

        setTimeout(function () {
            let prevItem;
            let isUsingTfControl = false;

            me.obControls.saveState();

            me.tfControls.addEventListener('mouseDown', function () {
                prevItem = new AssetItem(me.selectedItem);
                me.disableViewController();
            });

            me.tfControls.addEventListener('objectChange', function () {
                isUsingTfControl = !!me.selectedItem;
            });

            me.tfControls.addEventListener('mouseUp', function () {
                me.selectedItem.syncAssetInfoAndObject3D();

                if (isUsingTfControl) {
                    isUsingTfControl = false;

                    const transformHistory = new TransformHistory(me.selectedItem, prevItem, me.selectedItem);
                    me.__pushTransformHistory([transformHistory]);

                    setTimeout(function () {
                        me.options.onMove([me.selectedItem], me);
                    }, 100);
                }

                me.enableViewController();
            });

            me.obControls.domElement.addEventListener('mouseout', function () {
                me.disableViewController();
            });

            me.obControls.domElement.addEventListener('mouseenter', function () {
                me.enableViewController();
            });
        }, 100);
    }
}

// 전역에서 호출 가능하도록 추가
global.NemoArEditor = NemoArEditor;