class Property {
    constructor () {
        /**
         * 카메라 데이터파일 이름.
         */
        this.CAMERA_PARA_NAME = 'camera_para.dat';

        /**
         * 아이템이 배치되어 있는 그룹 이름.
         */
        this.ITEM_OBJECT_FIELD_NAME = 'itemObjectField';

        /**
         * 화면에 배치하는 필드 영역의 클래스명.
         */
        this.ELEMENT_FIELD_CLASS_NAME = 'nemo-ar-field';

        /**
         * 화면에 배치하는 필드 영역의 ID
         */
        this.ELEMENT_FIELD_ID = 'nemoArField';

        /**
         * 아이템 팝업 클래스명.
         */
        this.ITEM_POPUP_CLASS_NAME = 'nemo-item-popup';

        /**
         * 아이템 팝업의 백그라운드 클래스명.
         */
        this.ITEM_POPUP_BACKGROUND_CLASS_NAME = 'nemo-item-popup-background';

        /**
         * webcam 소스 타입.
         */
        this.SOURCE_TYPE_WEBCAM = 'webcam';

        /**
         * video 소스 타입.
         */
        this.SOURCE_TYPE_VIDEO = 'video';

        /**
         * 기본 넓이
         */
        this.DEFAULT_WIDTH = 640;

        /**
         * 기본 높이
         */
        this.DEFAULT_HEIGHT = 480;

        /**
         * 입력되는 소스의 폭.
         */
        this.SOURCE_WIDTH = 640;

        /**
         * 입력되는 소스의 높이.
         */
        this.SOURCE_HEIGHT = 480;

        /**
         * 소스가 화면에 보여지는 폭.
         */
        this.DISPLAY_WIDTH = 640;

        /**
         * 소스가 화면에 보여지는 높이.
         */
        this.DISPLAY_HEIGHT = 480;

        /**
         * 마커 추적모드의 타입. the mode of detection - ['color', 'color_and_matrix', 'mono', 'mono_and_matrix']
         */
        this.DETECTION_MODE = 'color_and_matrix';

        /**
         * 매트릭스 타입. [3x3, 3x3_HAMMING63, 3x3_PARITY65, 4x4, 4x4_BCH_13_9_3, 4x4_BCH_13_5_5]
         */
        this.MATRIX_CODE_TYPE = '4x4';

        /**
         * 화면이 갱신되는 시간.
         */
        this.DEFAULT_FPS_TIME = Math.floor(1000 / 30);

        /**
         * 1초 동안 추적되는 횟수.
         */
        this.MAX_DETECTION_RATE = 60;

        /**
         * 사용하지 않는 에셋이 화면에서 대기하는 시간.
         */
        this.ASSET_TIMEOUT = 300;

        // 파일 확장자.
        this.EXT_MTL = '.mtl';
        this.EXT_OBJ = '.obj';
        this.EXT_STL = '.stl';
        this.EXT_FBX = '.fbx';

        /**
         * 마커의 타입.
         */
        this.MARKER_TYPE = 'pattern';

         // 표시되는 에셋의 타입.
        this.ITEM_TYPE_NONE = 'none';
        this.ITEM_TYPE_3D_OBJ = 'obj';
        this.ITEM_TYPE_3D_STL = 'stl';
        this.ITEM_TYPE_3D_FBX = 'fbx';
        this.ITEM_TYPE_HTML = 'html';
        this.ITEM_TYPE_YOUTUBE = 'youtube';
        this.ITEM_TYPE_IMAGE = 'image';
        this.ITEM_TYPE_SPRITE = 'sprite';
        this.ITEM_TYPE_TEXT = 'text';

        /**
         * 대상 복제시 리로드 할 타입.
         */
        this.CLONE_RELOAD_TYPE = this.ITEM_TYPE_3D_FBX;

        /**
         * 로딩객체 사이즈.
         */
        this.LODING_OBJECT_SIZE = 0.25;

        /**
         * 바탕색.
         */
        this.STYLE_BACKGROUND_COLOR = '#DDDDDD';

        /**
         * 화면이 표시되는 최소거리.
         */
        this.CAMERA_NEAR = 0.01;

        /**
         * 화면이 표시되는 최대거리.
         */
        this.CAMERA_FAR = 500;

        /**
         * 에디터 카메라의 초기 위치.
         */
        this.CAMERA_ZERO_POSITION = new THREE.Vector3(0, 4, 10);

        /**
         * 에디터 카메라가 바라보는 지점.
         */
        this.CAMERA_ZERO_LOOK_AT = new THREE.Vector3(0, 0, 0);

        /**
         * 에디터 카메라의 초기 확대비율.
         */
        this.CAMERA_ZERO_ZOOM = 1;

        /**
         * 그리드 색.
         */
        this.GRID_COLOR = new THREE.Color(0x000000);

        /**
         * 그리드 사이즈.
         */
        this.GRID_SIZE = 100;

        /**
         * 그리드 간격.
         */
        this.GRID_STEP = 0.5;

        /**
         * 그리드 투명도.
         */
        this.GRID_OPACITY = 0.1;

        /**
         * 화면을 위 아래로 이동할 때의 최소 각도. (위쪽이 0도)
         */
        this.CONTROLS_MIN_POLAR_ANGLE = 0;

        /**
         * 화면을 위 아래로 이동할 때의 최대 각도. (위쪽이 0도)
         */
        this.CONTROLS_MAX_POLAR_ANGLE = Math.PI;

        /**
         * 이동 모드명.
         */
        this.CONTROLS_TF_MODE_POSITION = 'translate';

        /**
         * 회전 모드명.
         */
        this.CONTROLS_TF_MODE_ROTATION = 'rotate';

        /**
         * 크기 모드명.
         */
        this.CONTROLS_TF_MODE_SCALE = 'scale';

        /**
         * 조작 기준 영역 - 전역.
         */
        this.CONTROLS_TF_SPACE_W = 'world';

        /**
         * 조작 기준 영역 - 지역.
         */
        this.CONTROLS_TF_SPACE_L = 'local';

        /**
         * 위치이동모드.
         */
        this.EDITOR_MODE_TRANSLATE = 'translate';

        /**
         * 회전모드.
         */
        this.EDITOR_MODE_ROTATE = 'rotate';

        /**
         * 크기조절모드.
         */
        this.EDITOR_MODE_SCALE = 'scale';

        // webGlRenderer 창의 크기와 사용되는 카메라를 동일하게 사용해도 두 화면을 합쳤을 때 오차가 발생한다.
        // 이 오차는 화면 크기에 비례하여 달라진다.
        // 640 x 480 화면을 기준으로 일치하는 위치를 눈으로 측정했다. 보정 간격은 left : 4, top -2 로 나왔다.
        // 적용하기 위한 비율은 대략 left : 0.00625, top : 0.0042 가 나왔다.
        /**
         * webGlRenderer 의 창 크기 비례하여 cssRenderer가 움직여야 하는 보정비율
         */
        this.CSSRENDERER_POSITION_GAP_X = 0.00625;

        /**
         * webGlRenderer 의 창 크기 비례하여 cssRenderer가 움직여야 하는 보정비율
         */
        this.CSSRENDERER_POSITION_GAP_Y = 0.0042;

        /**
         * 3D객체와 유튜브 콘텐츠의 위치 보정값.
         */
        this.CSSRENDERER_YOUTUBE_GAP_TOP = 5;

        /**
         * 유튜브 플레이어 기본 사이즈.
         */
        this.YOUTUBE_DEFAULT_W = 480;

        /**
         * 유튜브 플레이어 기본 사이즈.
         */
        this.YOUTUBE_DEFAULT_H = 270;

        /**
         * html 내용이 표시되는 css필드의 클래스명.
         */
        this.CLASS_NAME_HTML_CONTENT = 'asset-html-content';

        /**
         * 에셋 미리 불러오기 개수.
         */
        this.LOAD_CACHING_COUNT = 0;

        /**
         * AR위치 매트릭스 평균측정 사이즈.
         */
        this.MATRIX_AVERAGE_SIZE = 5;

        /**
         * 패턴 테두리 안에 있는 이미지의 크기비율. 0.9 ~ 0.1 수치가 작아질 수록 검은 테두리가 두꺼워 진다.
         */
        this.PATTERN_RATIO = 0.5;

        /**
         * CSS랜더링 객체를 보여주는 마스크 3D객체의 이름.
         */
        this.TRANSPARENT_OBJECT_NAME = 'transparentObject3D';

        /**
         * 로드시 투명 여부가 저장되는 이름.
         */
        this.ORG_TRANSPARENT_NAME = 'orgTransparentName';

        /**
         * 로드시 불투명값이 저장되는 이름.
         */
        this.ORG_OPACITY_NAME = 'orgOpacityName';

        /**
         * 부드럽게 숨겨지는 간격.
         */
        this.SMOOTH_HIDE_INTERVAL = 0.2;
    }
}

// 간단한 형태의 정적 변수를 제공하지 않기 때문에 객체를 반환한다.
export default new Property();