export default class CssUnitV {
    constructor (assetItem) {
        this.webglAsset = assetItem.parentAsset;
        this.assetItem = assetItem;
        this.cssObject3D = null;
        this.ytPlayer = null;

        this.onReady = function () {};
    }

    // 에디터에서 지정한 위치, 크기, 방향은 AssetCssRenderer.__add() 에서 로컬 위치로 세팅된 상태.
    // 아이템을 감싸고 있는 그룹과 AR에셋을 동기화 한다.
    update() {
        const me = this;

        if (me.cssObject3D) {
            if (me.assetItem.isVisible) {
                const cssObject3D = me.cssObject3D;
                const resolution = 100000;
                const webglObject3D = me.webglAsset.object3D;
                const scale = webglObject3D.scale;
                const position = webglObject3D.position;
                const rotation = webglObject3D.rotation;

                cssObject3D.scale.set(scale.x * resolution, scale.y * resolution, scale.z * resolution);
                cssObject3D.position.set(position.x * resolution, position.y * resolution, position.z * resolution);
                cssObject3D.rotation.copy(rotation);

                me.showCssObject3D();

            } else {
                me.hideCssObject3D();
            }
        }
    }

    showObject3D(callEvent = true) {
        const me = this;
        me.assetItem.show(callEvent);
    }

    hideObject3D(callEvent = true) {
        const me = this;
        me.assetItem.hide(callEvent);
    }

    showCssObject3D() {
        const me = this;
        me.cssObject3D.children[0].element.style.display = '';
    }

    hideCssObject3D() {
        const me = this;
        me.cssObject3D.children[0].element.style.display = 'none';
    }
}