export default class Utils {
    constructor () {}

    static randomString(count = 16) {
        let str = '';

        for (let i = 0; i < count; i++) {
            str += Math.floor(Math.random() * 36).toString(36);
        }

        return str;
    }

    static createDivElement() {
        const el = document.createElement('DIV');

        el.style.color = '#000000';
        el.style.background = 'initial';

        return el;
    }

    static parseUrl(url) {
        const aEl = document.createElement('A');

        aEl.href = url;

        const arr = aEl.search.replace(/^\?/, '').split('&');
        const searchObj={};

        let queries;

        for (let i = 0; i < arr.length; i++) {
            queries = arr[i].split('=');
            searchObj[queries[0]] = queries[1];
        }

        return {
            protocol: aEl.protocol,
            host: aEl.host,
            hostname: aEl.hostname,
            port: aEl.port,
            // 브라우저에 따라서 pathname 값 앞에 `/`가 붙지 않는 겨우가 있다.
            // 브라우저 상관없이 `/`가 붙어있도록 처리한다.
            pathname: ('/' + aEl.pathname).replace(/^\/\//, '\/'),
            search: aEl.search,
            searchObj: searchObj,
            hash: aEl.hash
        };
    }

    static fileToUrl(file) {
        return new Promise(function (resolve, reject) {
            const reader = new FileReader();

            reader.onload = function (evt) {
                return resolve(evt.currentTarget.result);
            };

            reader.onerror = function (error) {
                console.error(error);
                return reject(error);
            };

            reader.readAsDataURL(file);
        });
    }

    static convertNonTransparentImg(imgFile) {
        return new Promise(function (resolve, reject) {
            const image = new Image();

            image.onload = function () {
                const canvas = document.createElement('canvas');
                const ctx = canvas.getContext('2d');

                canvas.width = image.width;
                canvas.height = image.height;

                ctx.fillStyle = '#ffffff';
                ctx.fillRect(0, 0, canvas.width, canvas.height);

                ctx.drawImage(image, 0, 0);

                resolve(canvas.toDataURL('image/png'));
            };

            image.onerror = function (error) {
                console.error(error);
                return reject(error);
            };

            image.src = URL.createObjectURL(imgFile);
        });
    }

    static sizeFromImageUrl(url) {
        return new Promise(function (resolve, reject) {
            const image = new Image();

            image.onload = function () {
                resolve({
                    url: url,
                    width: image.width,
                    height: image.height,
                });
            };

            image.onerror = function (error) {
                console.error(error);
                return reject(error);
            };

            image.src = url;
        });
    }
}